<template>
  <div class="row" v-if="isReady && !gettingOrder">
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 mr-2 ml-2">{{ order.date }}</h5>
          </div>
          <div class="flex-align-center flex-end">
            <el-button
              v-if="startEditing && !gettingOrder"
              @click="discardEditingOrder"
              type="primary"
              v-ripple.400
              class="flat"
            >
              Discard
            </el-button>
            <el-button
              v-if="startEditing && !gettingOrder"
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Put"
              type="primary"
            >
              Save
            </el-button>
            <el-button
              :loading="removing"
              v-ripple.400
              @click="Remove"
              type="danger"
            >
              Remove
            </el-button>
          </div>
        </div>
        <div class="col-lg-8 pr-lg-0">
          <div class="box">
            <div class="flex-x-between">
              <p class="mt-0 mb-2 text-bold">Items</p>

              <div class="d-flex align-items-center">
                <p
                  @click="addItem"
                  class="mt-0 pointer primary hover-underline"
                >
                  <i class="bx bx-plus mr-1"></i>Add item
                </p>
              </div>
            </div>
            <div class="row">
              <div class="col-12" v-if="order.products.length > 0">
                <el-collapse>
                  <el-collapse-item
                    v-for="(product, index) in order.products"
                    :key="index"
                    :name="index"
                  >
                    <template slot="title">
                      <div class="flex-x-between w-100 collapse-header">
                        <p>{{ product.name }} - {{ product.subtotal }}Ks</p>
                        <p
                          @click="removeProduct(index)"
                          class="danger hover-underline"
                          style="margin-right: 8px; z-index: 10; display: none"
                        >
                          <i class="bx bx-x"></i>
                        </p>
                      </div>
                    </template>
                    <div class="row mb-3">
                      <div class="col-12 mb-3">
                        <label for="Media" class="custom-label">Media</label>
                        <file
                          :key="index + product.name"
                          @completed="setImage($event, index)"
                          :initialData="
                            product.image ? [{ image: product.image }] : []
                          "
                        />
                      </div>
                      <div
                        class="col-12 flex-align-center mb-3"
                        v-if="product.sale"
                      >
                        <div>
                          <label for="Name" class="custom-label"
                            >Customer</label
                          >
                          <p
                            @click="
                              $router.push({
                                name: 'Edit order',
                                params: { id: product.sale },
                              })
                            "
                            class="primary pointer hover-underline"
                          >
                            {{ product.customer_name }}
                          </p>
                        </div>
                      </div>
                      <div class="col-6 mb-3">
                        <label for="Name" class="custom-label">Name</label>
                        <el-input
                          class="w-100"
                          name="Name"
                          v-model="product.name"
                        ></el-input>
                      </div>
                      <div class="col-6 mb-3">
                        <label for="Link" class="custom-label">Link</label>
                        <el-input
                          class="w-100"
                          name="Link"
                          v-model="product.link"
                        ></el-input>
                      </div>
                      <div class="col-6">
                        <label for="Link" class="custom-label">Quantity</label>
                        <el-input-number
                          :min="0"
                          class="w-100"
                          controls-position="right"
                          v-model="product.quantity"
                          @change="changeQuantity(index)"
                        ></el-input-number>
                      </div>
                      <div class="col-6">
                        <label for="Link" class="custom-label">Price</label>
                        <el-input
                          class="w-100"
                          type="number"
                          v-model="product.price"
                          @change="changeQuantity(index)"
                        ></el-input>
                      </div>
                    </div>
                  </el-collapse-item>
                </el-collapse>
              </div>
              <el-empty class="w-100" description="No item found!" v-else>
                <template #image> <empty /> </template
              ></el-empty>
            </div>
          </div>

          <div class="box mt-base">
            <div class="row">
              <div class="col-12">
                <p class="mt-0 mb-2 text-bold">Pricing</p>
                <div class="flex-x-between">
                  <p class="">Total</p>
                  <p class="">
                    {{ parseInt(order.total) === 0 ? "-" : order.total }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-4">
          <div class="box">
            <label for="" class="custom-label md-text"
              >Date of order(sync from date)</label
            >
            <el-date-picker
              class="w-100 mb-3"
              v-model="order.date"
              disabled
              type="date"
              format="dd/MM/yyyy"
              value-format="dd/MM/yyyy"
              placeholder="Pick a date"
            >
            </el-date-picker>

            <label for="" class="custom-label md-text">Supplier</label>
            <el-select
              v-model="order.supplier_name"
              @change="changeSupplier"
              filterable
              remote
              allow-create
              reserve-keyword
              class="w-100 mb-3"
              placeholder="Select or create new"
              :remote-method="getSuppliers"
              :loading="loadingSupplier"
            >
              <el-option
                v-for="supplier in suppliers"
                :key="supplier.id"
                :label="supplier.name"
                :value="supplier.name"
              >
              </el-option>
            </el-select>

            <label class="custom-label mb-0">Order Link</label>
            <div class="flex-x-between mb-2">
              <el-input
                id="orderLink"
                class="mr-2"
                disabled
                v-model="linkUrl"
              ></el-input>

              <el-button class="flat no-width" @click="copyURL" type="primary"
                >Copy</el-button
              >
              <el-button class="ml-2 no-width" @click="openUrl" type="primary"
                >Open Link</el-button
              >
            </div>

            <label class="custom-label mb-0">Note</label>
            <el-input
              type="textarea"
              placeholder="Text such as facebook profile link, etc."
              :row="5"
              v-model="order.note"
            ></el-input>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { url, mediaUrl } from "../../../core/constant/data";
import { file, empty } from "../../common";

export default {
  data() {
    return {
      url: url,
      isCopied: false,
      linkUrl: "",
      openCustomProduct: false,
      mediaUrl: mediaUrl,
      openFulfillDialog: false,
      addingFulfill: false,
      openPayment: false,
      payment: {
        amount: "0",
        date: "",
        order_id: null,
      },
      suppliers: [],
      loadingSupplier: false,
      saving: false,
      gettingOrder: false,
      order: {
        products: [],
      },
      selectedCustomer: {},
      loading: null,
      startEditing: false,
      adding: false,
      due_amount: 0,
      query: "",
      removing: false,
    };
  },
  components: { file, empty },
  watch: {
    order: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
    locations: {
      handler: function (value) {
        this.order.location = value[0].id;
        this.order.location_name = value[0].name;
      },
      deep: true,
    },
  },
  mounted() {
    this.$store.commit("setIsReady", false);
    this.filterorder();
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    setImage($event, index) {
      if ($event.length > 0) {
        this.order.products[index].image = $event[0].image;
      }
    },
    openUrl() {
      window.open(this.linkUrl, "_blank").focus();
    },
    copyURL() {
      navigator.clipboard.writeText(this.linkUrl);
      this.isCopied = true;
      this.$noti.display({
        color: "success",
        message: "Copied to clipboard!",
      });
    },
    addItem() {
      this.order.products.push({
        name: "",
        quantity: "1",
        price: "0",
        image: "/media/default.png",
        subtotal: "0",
        link: "",
      });
      this.calcTotal();
    },
    removeProduct(index) {
      this.order.products.splice(index, 1);
      this.calcTotal();
    },
    getSuppliers(query) {
      this.loadingSupplier = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-suppliers/?query=" + query)
        .then((response) => {
          this.loadingSupplier = false;
          this.suppliers = response.data;
        })
        .catch((err) => {
          this.loadingSupplier = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeSupplier() {
      var check = this.suppliers.filter((data) => {
        return data.name == this.order.supplier_name;
      });
      if (check.length > 0) {
        this.selectedSupplier = check[0];
      }
    },

    calcTotal() {
      this.order.total = this.order.products.reduce((total, item) => {
        return total + parseInt(item.subtotal);
      }, 0);
    },
    changeQuantity(index) {
      this.order.products[index].subtotal =
        parseInt(this.order.products[index].price) *
        parseInt(this.order.products[index].quantity);
      this.calcTotal();
    },
    Remove() {
      this.removing = true;
      this.$axios
        .delete("order/?oid=" + this.order.id)
        .then(() => {
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! order has been successfully removed.",
          });
          this.$router.push({ name: "imports" });
        })
        .catch((err) => {
          this.removing = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    filterorder() {
      this.gettingOrder = true;
      this.$axios
        .get("order/?oid=" + this.$route.params.id)
        .then((response) => {
          this.order = response.data;
          this.orderBeforeEdit = JSON.stringify(this.order);
          setTimeout(() => {
            this.startEditing = false;
            this.gettingOrder = false;
          }, 1);
          this.linkUrl = `http://app-order.osassist.epizy.com/?sid=${this.store.id}&oid=${response.data.id}&date=${response.data.date}`;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    Put() {
      this.saving = true;
      this.order.store = this.store.id;
      this.$axios
        .put("order/", { data: this.order })
        .then((res) => {
          this.order = res.data;
          this.orderBeforeEdit = JSON.stringify(this.order);
          setTimeout(() => {
            this.startEditing = false;
            this.adding = false;
            this.saving = false;
          }, 100);
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! order has been successfully updated.",
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    discardEditingOrder() {
      this.order = JSON.parse(this.orderBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>

<style>
a.text-flat {
  background: rgb(var(--input));
  padding: 9px 10px;
  margin-right: 10px;
  border-radius: 0.8rem;
}
</style>